<template>
     <div class="my-collect">
       <ul>
         <li v-for="item in collectList" >
           <product :leftWidth="600" :productInfo="item"></product>
         </li>
       </ul>

     </div>
</template>

<script>
import product from '@/components/BaseProduct'
export default {
  name: 'myCollect',
  components: {
    product
  },
  data () {
    return {
      collectList:[]
    }
  },
  mounted () {
    this.getCollectList()
  },
  methods: {
    getCollectList () {
      let postData = {
        id: 4,
        page: 1,
        limit: 2
      }
      this.$api.personCenter.getCollectList(postData).then(res => {
        if (res.code === 1) {
          this.collectList = res.data
        }
      })
    }
  }
}
</script>

<style scoped>
  .my-collect{
    background: #fff;
  }
</style>
